     color rgba(0, 0, 0, 0.8)\r\n            background #e9e9eb\r\n        &.transition\r\n          .op-icon\r\n            transition all 0.2s linear 0s\r\n      .v-right-item\r\n        text-align right\r\n        padding-right 6px\r\n        max-width 30%\r\n      .v-left-item\r\n        text-align left\r\n        padding-left 6px\r\n    .v-note-panel\r\n      position relative\r\n      border-top none\r\n      display flex\r\n      flex 1\r\n      width 100%\r\n      box-sizing border-box\r\n      overflow hidden\r\n      .v-note-edit.divarea-wrapper\r\n        flex 0 0 50%\r\n        width 50%\r\n        padding 0\r\n        overflow-y scroll\r\n        overflow-x hidden\r\n        box-sizing border-box\r\n        cursor text\r\n        border-bottom-left-radius: 4px;\r\n        scrollbar()\r\n        &.transition\r\n          transition all 0.2s linear 0s\r\n        &.single-edit\r\n          width 100%\r\n          flex 0 0 100%\r\n          overflow-y auto\r\n        &.single-show\r\n          width 0\r\n          flex 0 0 0\r\n          display none\r\n        .content-div\r\n          width 100%\r\n          padding 20px 25px\r\n          box-sizing border-box\r\n          outline 0 none\r\n          border none !important\r\n          color #2c3e50\r\n          font-size 16px\r\n        .content-input-wrapper\r\n          width 100%\r\n          padding 8px 25px 15px 25px\r\n      .v-note-show\r\n        flex 0 0 50%\r\n        width 50%\r\n        overflow-y auto\r\n        padding 0 0\r\n        transition all 0.2s linear 0s\r\n        &.single-show\r\n          flex 0 0 100%\r\n          width 100%\r\n        .v-show-content, .v-show-content-html\r\n          width 100%\r\n          height 100%\r\n          padding 8px 25px 15px 25px\r\n          overflow-y auto\r\n          box-sizing border-box\r\n          overflow-x hidden\r\n          scrollbar()\r\n      .v-note-navigation-wrapper\r\n        display flex\r\n        position absolute\r\n        width 250px\r\n        right 0\r\n        top 0\r\n        bottom 0\r\n        height 100%\r\n        flex-direction column\r\n        background-color rgba(255,255,255,0.98)\r\n        border-left border-color\r\n        border-right border-color\r\n        &.transition\r\n          transition all 0.1s linear 0s\r\n        @media only screen and (max-width 768px)\r\n          width 50%\r\n        &.slideTop-enter-active, &.slideTop-leave-active\r\n          height 100%\r\n        &.slideTop-enter, &.slideTop-leave-active\r\n          height 0\r\n        .v-note-navigation-title\r\n          height 50px\r\n          width 100%\r\n          border-bottom border-color\r\n          flex none\r\n          line-height @height\r\n          font-size 16px\r\n          box-sizing border-box\r\n          padding 0 12px 0 18px\r\n          .v-note-navigation-close\r\n            float right\r\n            color #606266\r\n            font-size 18px\r\n            cursor pointer\r\n            &:hover\r\n              color #303133\r\n        .v-note-navigation-content\r\n          overflow-y auto\r\n          flex 1\r\n          scrollbar()\r\n          padding 8px 0\r\n          h1, h2, h3, h4, h5, h6\r\n            margin 2px 0\r\n            font-weight 500\r\n            font-size 17px\r\n            color #2185d0\r\n            cursor pointer\r\n            line-height normal\r\n            overflow hidden\r\n            text-overflow ellipsis\r\n            white-space nowrap\r\n            padding 0 12px\r\n            border-bottom none\r\n            &:hover\r\n              color #483D8B\r\n              text-decoration-line underline\r\n          h2\r\n            padding-left  27px\r\n            font-size 17px\r\n          h3\r\n            padding-left  42px\r\n            font-size 17px\r\n          h4\r\n            padding-left  58px\r\n            font-size 15px\r\n          h5\r\n            padding-left  72px\r\n            font-size 15px\r\n          h6\r\